.main {
  background-color: #f4f4f4;
  padding-bottom: 16px;
  font-family: "Microsoft YaHei";
  overflow: auto;
}
.main .header {
  height: 120px;
  background: url("./assets/images/headbg.jpg") center bottom no-repeat;
  background-color: #fff;
}
.main .header-content {
  width: 100%;
  max-width: 1200px;
  height: 120px;
  margin: 0 auto;
  display: flex;
}
.main .header-content .title {
  color: #000;
  font-size: 28px;
  font-weight: bold;
  width: 100%;
  line-height: 120px;
  text-align: center;
}
.main .content {
  width: 100%;
  height: 536px;
  min-height: calc(100vh - 120px - 16px - 16px - 2px);
  max-width: 1200px;
  margin: 16px auto 0;
  display: flex;
  justify-content: space-between;
}
.main .content .params-container {
  width: 340px;
  height: 100%;
  padding: 16px;
  position: relative;
  box-shadow: #ddd 0 0 10px;
  background-color: #fff;
  /*.icon-info {
        color: #777;
        font-size: 16px;
        transition: color .3s;
        margin-left: 4px;

        &:hover {
          color: @btn_hover_color;
        }
      }
      */
}
.main .content .params-container .param {
  margin: 0 0 16px;
}
.main .content .params-container .param-label,
.main .content .params-container .param .map-param-label {
  display: inline-block;
  width: 100px;
  text-align: right;
}
.main .content .params-container .param-label::before {
  content: "*";
  color: red;
  position: relative;
  right: 5px;
}
.main .content .params-container .ant-input-affix-wrapper,
.main .content .params-container .ant-input-number,
.main .content .params-container .ant-picker,
.main .content .params-container .ant-select,
.main .content .params-container .ant-radio-group {
  width: 180px;
}
.main .content .params-container .triple-input {
  display: inline-flex;
  width: 180px;
  justify-content: space-between;
}
.main .content .params-container .triple-input .ant-input-number {
  width: calc((100% - 6px) / 3);
}
.main .content .params-container .triple-input .ant-input-number-input {
  padding: 0 2px;
}
.main .content .params-container .ant-radio-wrapper {
  margin-right: 30px;
}
.main .content .params-container .calculate-btn {
  min-width: 100px;
  height: 44px;
}
.main .content .map-container {
  width: calc(100% - 340px - 16px);
  height: 100%;
  box-shadow: #ddd 0 0 10px;
  background-color: #fff;
}
.main .content .map-container .amap-container .amap-logo,
.main .content .map-container .amap-container .amap-copyright,
.main .content .map-container .amap-container .amap-maptype-list {
  display: none !important;
}
.main .content .map-container .amap-container .amap-maptype-title {
  width: 100%;
}
.main .content .map-container .amap-container .amap-scale-text {
  color: #fff;
  font-size: 12px;
}
.main .content .map-container .marker {
  width: 16px;
  height: 16px;
  position: relative;
}
.main .content .map-container .marker .circle {
  height: 16px;
  border-radius: 50%;
  background-color: red;
}
.main .content .map-container .marker .circle:before {
  content: "";
  display: block;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #fff;
  position: relative;
  top: 5px;
  left: 5px;
}
.main .content .map-container .marker .tran {
  width: 0;
  height: 0;
  border-radius: 50%;
  border-right: 8px solid transparent;
  border-left: 8px solid transparent;
  border-top: 10px solid red;
  position: relative;
  top: -5px;
}
.main .content .map-container .marker-name {
  position: absolute;
  width: 100px;
  color: #fff;
}
.main .content .data-container {
  width: 400px;
  border: 1px solid #f0f0f0;
}
.main .content .data-container .controller-area {
  width: 100%;
  height: 36px;
  line-height: 36px;
  text-align: right;
  background-color: #dde8f1;
  border-bottom: 1px solid #f0f0f0;
}
.main .content .data-container .controller-area .anticon {
  cursor: pointer;
  transition: color 0.3s;
  font-size: 16px;
  margin-right: 16px;
}
.main .content .data-container .controller-area .anticon:hover {
  color: #029ce9;
}
.main .content .data-container .ant-input {
  height: calc(100% - 36px);
}
.main .content .data-container .react-monaco-editor-container {
  height: calc(100% - 36px) !important;
}
.main .content .data-container .react-monaco-editor-container .monaco-editor {
  width: 100% !important;
  height: 100% !important;
}
.main .content .data-container .react-monaco-editor-container .overflow-guard {
  width: 100% !important;
}
.main .content .chart-container {
  height: 100%;
  min-height: 500px;
  padding: 16px;
  background: #fff;
  width: calc(100% - 400px);
  border: 1px solid #f0f0f0;
  border-left: none;
  position: relative;
}
.main .content .chart-container .chart-title {
  width: 100%;
  text-align: center;
  font-weight: bold;
  font-size: 16px;
  position: absolute;
}
.main .content .chart-container #container {
  width: 100%;
  height: 100%;
}
.main .content .chart-container #container canvas {
  height: 100% !important;
}
.main .storage-btn,
.main .model-btn,
.main .back-btn {
  position: fixed;
  left: 0;
  width: 40px;
  white-space: unset;
  padding: 4px 10px;
  border-radius: 0 2px 2px 0;
  z-index: 999;
}
.main .storage-btn {
  top: 136px;
  height: 160px;
}
.main .model-btn {
  top: 312px;
  height: 120px;
}
.main .back-btn {
  top: 136px;
  height: 120px;
}
.custom-btn {
  background-color: #1a6bb7;
  border-color: #1a6bb7;
}
.custom-btn:hover {
  background-color: #029ce9;
  border-color: #029ce9;
}
.ant-result {
  padding: 16px;
}
.calc-storage-table thead .ant-table-cell {
  padding: 16px 0;
}
.calc-storage-table tbody .ant-table-cell {
  padding: 10px 0;
}
.calc-storage-table .icon-delete,
.calc-storage-table .icon-view {
  transition: all 0.3s;
  cursor: pointer;
}
.calc-storage-table .icon-delete:hover {
  color: red;
}
.calc-storage-table .icon-view:hover {
  color: #029ce9;
}
.ant-table-title {
  text-align: center;
  font-weight: bold;
}
.ant-table-thead .ant-table-cell {
  text-align: center;
}
.calcdrawer .ant-drawer-content-wrapper {
  width: 100% !important;
  max-width: 800px;
}
.modeldrawer .ant-drawer-content-wrapper {
  width: 100% !important;
  max-width: 500px;
}
.ant-picker-now {
  display: none !important;
}
.stg-details-table-container {
  display: flex;
  justify-content: space-between;
}
.stg-details-table-container .stg-details-table {
  margin-bottom: 16px;
}
.ant-modal-wrap {
  display: flex;
  align-items: center;
}
.ant-modal-wrap .ant-modal {
  position: unset;
  top: unset;
  padding-bottom: 0;
}
@media (max-width: 768px) {
  .main .header-content .title {
    font-size: 24px;
  }
  .main .calculate-content {
    flex-direction: column-reverse;
    justify-content: flex-end;
    height: auto;
    min-height: 676px;
    background-color: #fff;
  }
  .main .calculate-content .params-container {
    width: 420px;
    margin: 0 auto;
    box-shadow: unset;
  }
  .main .calculate-content .params-container .ant-input-affix-wrapper,
  .main .calculate-content .params-container .ant-input-number,
  .main .calculate-content .params-container .ant-picker,
  .main .calculate-content .params-container .ant-select,
  .main .calculate-content .params-container .ant-radio-group {
    width: 260px;
  }
  .main .calculate-content .params-container .triple-input {
    width: 260px;
  }
  .main .calculate-content .map-container {
    width: 100%;
    height: 200px;
    box-shadow: unset;
  }
  .main .datavis-content {
    flex-direction: column-reverse;
    height: 1200px;
  }
  .main .datavis-content .data-container,
  .main .datavis-content .chart-container {
    width: 100%;
    height: 50%;
  }
  .main .storage-btn,
  .main .model-btn,
  .main .back-btn {
    height: 40px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .main .storage-btn .anticon,
  .main .model-btn .anticon,
  .main .back-btn .anticon {
    height: 18px;
    line-height: 18px;
    vertical-align: unset;
  }
  .main .model-btn {
    top: 192px;
  }
}
@media (max-width: 576px) {
  .main .header {
    height: 60px;
  }
  .main .header .header-content {
    height: 60px;
  }
  .main .header .header-content .logo {
    height: 50px;
    margin-top: 5px;
  }
  .main .header .header-content .title {
    line-height: 60px;
    font-size: 20px;
  }
  .main .content {
    margin-top: 0;
  }
  .main .content .params-container {
    width: 100%;
    max-width: 420px;
    padding-top: 6px;
  }
  .main .content .params-container .param {
    margin-bottom: 6px;
  }
  .main .content .params-container .triple-input {
    max-width: calc(100% - 130px);
  }
  .main .content .params-container .triple-input .ant-input-number {
    max-width: unset;
  }
  .main .content .params-container .ant-input-affix-wrapper,
  .main .content .params-container .ant-input-number,
  .main .content .params-container .ant-picker,
  .main .content .params-container .ant-select,
  .main .content .params-container .ant-radio-group {
    max-width: calc(100% - 130px);
  }
  .main .content .params-container .ant-radio-wrapper {
    margin-right: 10px;
  }
  .main .storage-btn,
  .main .back-btn {
    top: 60px;
  }
  .main .model-btn {
    top: 108px;
  }
  .stg-details-table-container {
    display: unset;
  }
  .stg-details-table-container .stg-details-table {
    width: 100% !important;
  }
  .amap-controlbar {
    top: unset !important;
    bottom: -40px !important;
    right: 0;
    left: unset !important;
  }
  .ant-result {
    padding: 0 0 8px !important;
  }
}
.ant-result-subtitle {
  font-size: 16px !important;
  text-align: left !important;
}
.ant-result-subtitle p {
  margin-bottom: 0;
}
